@import '../../assets//styles/light.less';
.header {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 40px;
  background-color: #fafafa;
  background: linear-gradient(#FFFFFF,#F2F2F2);
  padding: 0 12px;
  box-shadow: 0 1px 5px rgba(0 0 0 / 13%) inset;
  border-bottom: 1px solid #d3d3d3;
}

.container {
  width: @pageWidth;
  margin: 0 auto;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.themeBtn  {
  align-content: flex-end;
  padding: 0 14px;
  color: #777777;
  font-size: 14px;
  font-weight: 600;
  height: 100%;
  line-height: 40px;
  transition: all 0.3s ease-in-out;
  &:hover {
    color: #333;
    cursor: pointer;
    background: #e5e5e5;
    box-shadow: 0 1px 5px rgb(0 0 0 / 13%) inset;
  }
}

.logo {
  padding: 0 20px 0 50px;
  background: url(../../assets//img/logo.gif);
  background-size: 44px;
  background-repeat: no-repeat;
  vertical-align: middle;
  font-size: 18px;
  color: #666;
}

.nav {
  flex: 1;
  display: inline-flex;
  justify-content: flex-start;
  &Item {
    padding: 0 14px;
    color: #777777;
    font-size: 14px;
    font-weight: 600;
    height: 100%;
    line-height: 40px;
    transition: all 0.3s ease-in-out;
    &Active {
      color: #555555;
      background: #E5E5E5;
      box-shadow: 0 1px 5px rgb(0 0 0 / 13%) inset;
    }
    &:hover {
      color: #333;
    }
  }
}

