@import '../../assets/styles/light.less';
.container {
  width: 400px;
  margin: 0 auto;
  padding-top: 60px;
}

.title {
  margin-top: 12px;
  text-align: center;
  font-weight: 500;
  color: @pageTitleColor;
}

.nav {
  margin: auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: @homeNavColor;
}

.navItem {
  text-align: center;
  margin: 30px 40px;
  &:hover {
    color: @homeNavHoverColor;
  }
  h1 {
    font-weight: 500;
    font-size: 36px;
    margin-bottom: 12px;
  }
  span {
    font-size: 18px;
  }
}

.footer {
  margin: 24px auto;
  text-align: center;
  font-size: 13px;
  color: @homeFooterColor;
  a {
    padding: 0 0 0 5px;
  }
  a:hover {
    background: @homeFooterHoverColor;
    color: #fff;
  }
}

.logo {
  height: 200px;
  width: 400px;
  img {
    max-height: 100%;
  }
}