@import '../../../assets/styles/light.less';
.main {
  width: @pageWidth;
  margin: 0 auto;
  padding-top: 40px;
  padding: 12px 12px;
}

.title {
  font-size: 20px;
  font-weight: 500;
  color: #555555;
  margin-bottom: 10px;
}

.btn {
  width: 52px;
  height: 18px;
  padding: 7px 12px;
  box-sizing: content-box;
  font-size: 13px;
  background: @btnColor;
  color: #fff;
  border: none;
  margin-top: 12px;
  transition: all .3s ease-in-out;
  &:hover {
    background: @btnHoverColor;
  }
}