.btnDark {
  button.dark {
    background: #cc0033;
    &:hover {
      background: #ff0033;
    }

  }
}

.textDark {
  // 字体
  .title.dark {
    color: #888888;
  }
  .home-nav.dark {
    color: #999999;
    a {
      &:hover {
        color: #cc0033;
      }
      span {
        background: transparent;
      }
    }
  }
  .footer.dark:hover {
    a {
      color: #ddd;
    background: #cc0033;
    }
  }
  .info-tip.dark {
    span {
      background: transparent;
    }
    color: #aaaaaa;
    div {
      border-color: #bb0033;
    }
  }
  .info.dark {
    div {
      border-color: #bb0033;
    }
  }
}

.inputBg {
  input, textarea {
    background: #333842;
    color: #dddddd;
    // outline: none;
    border: #424447 solid 1px;
    &&focus {
      outline: #2c313c solid 1px;
    }
  }
  label {
    color: #aaaaaa;
  }
  span {
    background: #4c505c;
  }
}

.headerDark {
  .header.dark {
    // background:#333842;
    background: linear-gradient(#303040,#333842);
    border-bottom: #282c34;
    box-shadow: 0 1px 5px rgba(0 0 0 / 60%);
  }
  .nav-item.dark {
    color: #757982;
    &:hover {
      color: #ddd
    }
  }
  .nav-item.active.dark {
    color: #d7dad6;
    background: #282c34;
  }
  .theme-btn.dark {
    color: #757982;
    &:hover {
      color: #d7dad6;
      background: #282c34;
      box-shadow: 0 1px 5px rgb(0 0 0 / 50%) inset;
    }
  }
}

#root[theme='dark'] {
  background: #21252b;
  .btnDark();
  .textDark();
  .headerDark();
  .inputBg();
}

