@import '../../assets/styles/light.less';
.main {
  width: @pageWidth;
  margin: 0 auto;
  margin-top: 40px;
  padding: 12px 12px;
  color: @pageTitleColor;
  .title {
    font-size: 20px;
    font-weight: 500;
    margin-bottom: 10px;
  }
}

.open {
  display: flex;
  align-items: center;
  margin-bottom: 30px;
  input {
    width: 220px;
    border: 1px solid #D9D9D9;
    padding: 8px;
    color: #333333;
    transition: all .3s ease-in-out;
    margin-right: 6px;
  }
  input:hover {
    box-shadow: 0 1px 3px rgba(0, 0, 0, .1) inset;
  }
  label {
    font-size: 13px;
    color: #333333;
    margin-right: 4px;
  }
  .btn {
    display: inline-block;
    width: 52px;
    height: 18px;
    padding: 7px 12px;
    box-sizing: content-box;
    font-size: 13px;
    background: @btnColor;
    color: #fff;
    border: none;
    transition: all .3s ease-in-out;
    &:hover {
      background: @btnHoverColor;
    }
  }
}

.error {
  background-color: #F5F5B5;
  border: 1px solid #DECA7E;
  padding: 8px;
  width: 400px;
  color: #900;
  font-size: 13px;
}