@import '../../../assets/styles/light.less';
.main {
  width: @pageWidth;
  margin: 0 auto;
  margin-top: 40px;
  padding: 12px;
}

.title {
  margin-bottom: 24px;
  color: @pageTitleColor;
}

.disp {
  margin-bottom: 10px;
  font-size: 13px;
  label {
    display: block;
    margin-bottom: 2px;
    font-size: 13px;
    color: @inputLabelColor;
  }
  input {
    display: inline-block;
    width: 220px;
    border: 1px solid #D9D9D9;
    padding: 8px;
    color: #333333;
    transition: all .3s ease-in-out;
  }
  .tip {
    font-size: 13px;
    color: #999;
    background-color: #f6f6f6;
  }
}